* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url(images/star.svg);
  margin-right: 4px;
  width: 16px;
  height: 16px;
  background-repeat: no-repeat;
  background-position: center;
}

.stars--1 :nth-child(-n + 1),
.stars--2 :nth-child(-n + 2),
.stars--3 :nth-child(-n + 3),
.stars--4 :nth-child(-n + 4),
.stars--5 :nth-child(-n + 5) {
  background-image: url(images/star-active.svg);
}

.stars--0 :nth-last-child(-n + 1),
.stars--1 :nth-last-child(-n + 1),
.stars--2 :nth-last-child(-n + 1),
.stars--3 :nth-last-child(-n + 1),
.stars--4 :nth-last-child(-n + 1),
.stars--5 :nth-last-child(-n + 1) {
  margin-right: 0;
}
